.slider__inspector {
  max-width: 100vw;
  margin-top: 109px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slider__caption {
  text-align: center;
}
.slider__caption span {
  max-width: 1064px;
  display: inline-block;
  background: linear-gradient(
    89.75deg,
    rgba(255, 255, 255, 0.5) 0.32%,
    #ffffff 72.39%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
}

.slider__container {
  max-width: 100vw;
  display: flex;
  margin-top: 120px;
  flex-direction: column;
  align-items: center;
}

.slider__card {
  width: 668px;
  min-width: 668px;
  max-width: 668px;
  background: linear-gradient(
    90.63deg,
    rgba(255, 255, 255, 0.04) 0.11%,
    rgba(255, 255, 255, 0) 99.9%
  );
  box-sizing: border-box;
  backdrop-filter: blur(6px);
  border: 1px solid rgba(255, 255, 255, 0.6);
  border-radius: 2px;
  overflow-y: hidden;
}
.slider__card .slider__text__wrapper {
  margin: 0 40px;
  z-index: 2;
  position: relative;
}
.slider__card .slider__text__wrapper p {
  margin-top: 24px;
  margin-bottom: 30px;
  color: #ffffff;
  font-family: "Mulish";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  width: 100%;
}
.slider__card .slider__stars {
  margin-top: 40px;
}
.slider__card .slider__rhombus__container {
  display: flex;
  align-items: center;
  margin-bottom: 28px;
}
.slider__card .slider__rhombus__container span {
  margin-left: 24px;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #40ff8c;
}

.slider__light {
  position: absolute;
  width: 668px;
  height: 600px;
  transform: translateY(150px);
  bottom: 0;
  background: url("/public/img/home/slider__light.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 0;
}

.slider__btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 60px;
  z-index: 3;
  margin-top: 20px;
}
.slider__btn span {
  display: inline-block;
  width: 200px;
  height: 20px;
}
@media screen and (max-width: 800px) {
  .slider__card {
    width: 450px;
    max-width: 450px;
    min-width: 450px;
  }
}
@media screen and (max-width: 600px) {
  .slider__inspector {
    transform: translateY(-300px);
  }
  .slider_card {
    width: 328px;
    max-width: 328px;
    min-width: 328px;
    backdrop-filter: blur(200px);
  }
  .slider__caption span {
    font-size: 24px;
    line-height: 24px;
  }
  .slider__container {
    margin-top: 40px;
  }
  .slider__btn {
    height: 48px;
    max-height: 48px;
  }
}
@media screen and (max-width: 400px) {
  .slider__caption span {
    font-size: 18px;
    line-height: 24px;
  }
}
