.footer {
  color:white;
  width: 100vw;
  box-sizing: border-box;
  height: 104px;
  padding: 0 14.75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top:auto;
}
.footer img {
  width: 80px;
  height: 80px;
}
.footer span {
  font-family: "Mulish";
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}
@media screen and (max-width: 400px) {
  .footer img{
  display:none;
  }
  .footer{
    padding: 0;
  justify-content: center;
  }
}