.postlist-bg {
  width: 100%;
  height: 100%;
  background-image: url("/public/img/postlist/header-bg.png");
  position: absolute;
  z-index: -1;
  background-size: auto;
  background-position: center;
  background-repeat: repeat;
}

.postlist-content {
  max-width: 100vw;
  padding: 0 14.75%;
  padding-top: 213px;
  position: relative;
  color: white;
  font-family: "Halvar Breitschrift";
  z-index: -1;
}

.content-a {
  color: #40ff8c;
  text-decoration: none;
}
.content-a .green-arrow-right {
  margin-right: 30px;
}

.post-card__inspector {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 40px;
  gap: 32px;
  margin-bottom: 32px;
}

.post-card__card {
  width: 422px;
  display: flex;
  max-height: 370px;
  overflow: hidden;
  flex-direction: column;
  box-sizing: border-box;
  padding-top: 32px;
  padding-left: 32px;
  padding-right: 40px;
  padding-bottom: 40px;
  backdrop-filter: blur(6px);
  position: relative;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0) 100%);
  border-image-slice: 1;
  border-image-width: 1px;
  text-decoration: none;
  color: white;
}

.post-card__card:hover p {
  color: #40ff8c;
  transition: 400ms;
}
.post-card__card:hover .post-card__description {
  color: #ffffff;
  transition: 400ms;
}
.post-card__card:hover .post-card__light {
  height: 72px;
}

.post-card__light {
  position: absolute;
  top: 0;
  width: 80%;
  height: 56px;
  opacity: 0.8;
  filter: blur(90px);
  transition: 400ms;
}

.yellow-card {
  background: #fef17b;
}

.blue-card {
  background: #5bb9fd;
}

.green-card {
  background: #78ffae;
}

.post-card__date {
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
  color: #b8becc;
}

.post-card__text {
  margin-top: 44px;
  margin-bottom: 10px;
}
.post-card__text p {
  margin-top: 0px;
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  max-height: 96px;
  transition: 400ms;
}
.post-card__text .post-card__description {
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
  color: #b8becc;
  height: 72px;
  max-height: 72px;
  overflow-y: hidden;
  display: inline-block;
  transition: 400ms;
}

.post-card__btn {
  font-weight: 600;
  font-family: "Mulish";
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  background: transparent;
  border: none;
  color: #40ff8c;
  width: fit-content;
  margin-top: auto;
  cursor: pointer;
}

@media screen and (max-width: 1250px) {
  .post-card__card {
    max-width: 380px;
  }
  .post-card__card p {
    font-size: 20px;
    line-height: 26px;
  }
}
@media screen and (max-width: 1150px) {
  .post-card__card {
    max-width: 350px;
  }
}
@media screen and (max-width: 1000px) {
  .post-card__card p {
    font-size: 20px;
    line-height: 26px;
  }
}
@media screen and (max-width: 600px) {
  .postlist-content {
    padding: 0 16px;
    padding-top: 162px;
  }
  .post-card__card {
    max-width: 328px;
    padding: 32px 16px 40px;
    height: 324px;
  }
  .post-card__card p {
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 24px;
  }
  .post-card__text {
    margin-top: 40px;
  }
  .content-a .green-arrow-right {
    margin-right: 20px;
  }
}