.modal__inspector {
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.384);
}
.modal__inspector.active {
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000000;
}
.modal__container {
  position: relative;
  width: 720px;
  height: 478px;
  max-height: 478px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
  font-size: 16px;
  line-height: 24px;
  background-image: url("/public/img/home/modal-bg.png");
  background-size: cover;
}
.modal__caption{
width:100%;
text-align: left;
}
.text__container {
  padding: 0 50px;
  max-width:100%;
  box-sizing: border-box;
}
.modal__description {
  margin-top: 30px;
}
.logo {
  position: absolute;
  z-index: 2;
}
.avatar {
  transform: translateY(-230px);
  position: absolute;
}
.btn {
  position: absolute;
  bottom: 0;
  transform: translateY(-48px);
}
@media screen and (max-width: 800px) {
  .modal__container{
  max-width:600px;
  max-height: 400px;
  }
  .avatar{
  transform: translateY(-215px);
  width:150px;
  height:150px;
  }
  .modal__caption, .modal__container{
  font-size:14px;
  }
  .btn{
  transform: translateY(-24px);
  }
}
@media screen and (max-width:600px) {
  .modal__container{
  max-width:400px;
  max-height:1000px;
  height: fit-content;
  box-sizing: border-box;
  padding:20px 0
  }
  .btn{
  position: relative;
  margin-top:20px
  }
  .avatar{
  transform: translateY(-100px);
  top:0
  }
}
@media screen and (max-width: 400px) {
  .avatar{
  width:120px;
  height:120px;
  transform: translateY(-85px);
  }
  p{
  line-height:17px
  }
}