.card-content__content {
  max-width: 100vw;
  padding: 0 14.75%;
  box-sizing: border-box;
  padding-top: 213px;
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 64px;
  position: relative;
  z-index: -1;
  font-family: "Halvar Breitschrift";
  color: white;
  background-image: url("/public/img/postlist/header-bg.png");
  background-repeat: no-repeat;
  background-position: center;
}

.card-description__inspector {
  grid-column: 1;
  max-width: 964px;
  width: 100%;
  border: 1px solid red;
  box-sizing: border-box;
  padding: 96px 100px 80px;
  margin-top: 40px;
  margin-bottom: 112px;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  backdrop-filter: blur(6px);
  border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0) 100%);
  border-image-slice: 1;
  border-image-width: 1px;
}

.card-content__main-title {
  font-size: 36px;
  line-height: 48px;
  color: white;
  font-weight: 700;
}

.card-content__date {
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
  color: #b8becc;
  margin-top: 33px;
}

.card-content__description {
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
  margin-top: 40px;
}

.card-content__title {
  font-size: 24px;
  line-height: 32px;
  color: #40ff8c;
  font-weight: 700;
  margin-top: 40px;
}

.card-content__bitcoin {
  width: 764px;
  height: 320px;
  margin-top: 32px;
  background-image: url("/public/img/cardcontent/bitcoin.png");
  background-size: cover;
  background-position: center;
}

.card-content__marker {
  background: url("/public/img/home/subtract.png");
  display: inline-block;
  width: 32px;
  height: 32px;
  background-position: center;
  background-size: 50px;
  margin-right: 16px;
  transform: translateY(4px);
}

.card-content__list {
  list-style-type: none;
  margin-top: 24px;
}
.card-content__list li {
  display: flex;
  align-items: center;
  height: fit-content;
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
}

.margin {
  margin-top: 24px;
}

.card-content__aside {
  display: flex;
  flex-direction: column;
  width: fit-content;
}
.card-content__aside .card-content__aside-title {
  margin-bottom: 40px;
}

.card-content__card-inspector {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: fit-content;
}

.card-content__card {
  width: 328px;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  box-sizing: border-box;
  backdrop-filter: blur(6px);
  position: relative;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  border-image-source: linear-gradient(180deg, rgba(255, 255, 255, 0.6) 0%, rgba(255, 255, 255, 0) 100%);
  border-image-slice: 1;
  border-image-width: 1px;
  text-decoration: none;
  max-width: 328px;
  padding: 32px 16px 40px;
  height: 324px;
}
.card-content__card .card-content__date {
  margin: 0;
}

.card-content__text {
  margin-top: 40px;
  margin-bottom: 10px;
  color: #b8becc;
}
.card-content__text .card-content__title-card {
  margin-top: 0px;
  font-weight: 700;
  max-height: 96px;
  color: white;
  margin-bottom: 16px;
  font-size: 18px;
  line-height: 24px;
  transition: 400ms;
}
.card-content__text .card-content__description-card {
  font-size: 16px;
  line-height: 24px;
  font-family: "Mulish";
  font-style: normal;
  color: #b8becc;
  height: 72px;
  max-height: 72px;
  overflow-y: hidden;
  display: inline-block;
  transition: 400ms;
}

.card-content__card:hover .card-content__title-card {
  color: #40ff8c;
  transition: 400ms;
}
.card-content__card:hover .card-content__description-card {
  color: #ffffff;
  transition: 400ms;
}
.card-content__card:hover .card-content__light {
  height: 72px;
}

.card-content__light {
  position: absolute;
  top: 0;
  width: 80%;
  height: 56px;
  opacity: 0.8;
  filter: blur(90px);
  transition: 400ms;
}

.card-content__btn {
  font-weight: 600;
  font-family: "Mulish";
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  background: transparent;
  border: none;
  color: #40ff8c;
  width: fit-content;
  margin-top: auto;
  cursor: pointer;
}

@media screen and (max-width: 1600px) {
  .card-description__inspector {
    margin-bottom: 50px;
  }
  .card-content__content {
    display: grid;
    grid-template-columns: auto;
    gap: 0px;
  }
  .card-content__card-inspector {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .card-content__aside-title {
    text-align: center;
  }
}
@media screen and (max-width: 1480px) {
  .card-content__aside {
    margin-bottom: 112px;
    gap: 24px;
  }
}
@media screen and (max-width: 1360px) {
  .card-description__inspector {
    padding: 56px 60px 40px;
  }
}
@media screen and (max-width: 1240px) {
  .card-description__inspector {
    padding: 26px 30px 10px;
  }
}
@media screen and (max-width: 1170px) {
  .card-description__inspector {
    padding: 44px 20px 80px;
    max-width: 670px;
  }
  .card-content__bitcoin {
    width: 600px;
  }
  .card-content__main-title {
    font-size: 24px;
    line-height: 32px;
  }
}
@media screen and (max-width: 900px) {
  .card-description__inspector {
    max-width: 600px;
  }
  .card-content__description {
    font-size: 14px;
    line-height: 20px;
  }
  .card-content__bitcoin {
    width: 500px;
    height: 320px;
  }
}
@media screen and (max-width: 700px) {
  .card-content__content {
    padding-right: 25px;
    padding-left: 25px;
  }
  .card-description__inspector {
    margin-left: auto;
    margin-right: auto;
  }
  .card-content__main-title {
    font-size: 18px;
    line-height: 24px;
  }
  .card-content__title {
    font-size: 16px;
    line-height: 24px;
  }
  .card-content__list span {
    font-size: 14px;
    line-height: 20px;
  }
  .card-content__list .card-content__marker {
    width: 23px;
    height: 23px;
    background-size: 30px;
    margin-right: 12px;
  }
}
@media screen and (max-width: 600px) {
  .card-description__inspector {
    margin-bottom: 20px;
  }
  .card-description__inspector {
    max-width: 450px;
  }
  .card-content__bitcoin {
    width: 100%;
    background-image: url("/public/img/cardcontent/bitcoin-mobile.png");
    height: 320px;
  }
  .card-content__list .card-content__marker {
    max-width: 16px;
    max-height: 16px;
    background-size: 20px;
  }
}
@media screen and (max-width: 400px) {
  .card-content__content {
    padding-right: 10px;
    padding-left: 10px;
  }
  .card-description__inspector {
    width: 310px;
  }
  .card-content__card {
    width: 310px;
  }
}