.btn{
  font-family: "Halvar Breitschrift";
  font-weight: 700;
  background-image: url('/public/img/btn1.png');
  background-size: cover;
  background-color: transparent;
  color: #40ff8c;
  font-size: 16px;
  line-height: 24px;
  border: none;
  width:220px;  
  height:56px;
  transition: 200ms;
  cursor: pointer;
}
.btn:hover{
  background-image: url("/public/img/btnbgg.png");
  transition: 200ms;
}