.window {
  width:668px;
  overflow:hidden;
}
.all_items_container {
  height: 100%;
  display: flex;
  flex-direction: row;
  transition: translate;
  transition-property:transform;
  transition-duration: 300ms;
}
@media screen and (max-width:800px) {
  .window{
  width:450px
  }
}

@media screen and (max-width: 600px) {
  .window{
  width:328px
  }
}