@import url("https://fonts.googleapis.com/css2?family=Mulish&display=swap");
@font-face {
  font-family: "Halvar Breitschrift";
  src: url("/src/components/assets/fonts/Halvar/HalvarBreit-Lt.ttf");
}
ul {
  margin: 0px;
  padding: 0px;
}

.navbar__inspector {
  height: 108px;
  color: rgba(255, 255, 255, 0.753);
  position: absolute;
  margin-top: 40px;
}
.navbar__inspector li select {
  border: none;
  color: rgba(255, 255, 255, 0.5);
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  font-family: "Halvar Breitschrift";
}
.navbar__inspector li select option {
  color: black;
}

.navbar__ul {
  display: flex;
  width: 100vw;
  box-sizing: border-box;
  justify-content: space-between;
  padding: 0px 14.6%;
  align-items: center;
  height: 100%;
}

.navbar__li {
  list-style-type: none;
  height: fit-content;
  font-family: "Mulish", sans-serif;
  text-decoration: none;
}

.navbar__select {
  list-style-type: none;
  font-family: "Mulish", sans-serif;
  text-decoration: none;
  transition: 300ms;
  height: 22px;
  align-self: flex-start;
  margin-top: 44px;
  overflow-y: hidden;
  text-align: center;
  min-width: 80px;
  text-transform: uppercase;
  cursor: pointer;
}
.navbar__select a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.753);
}
.navbar__select p {
  margin: 0;
  box-sizing: border-box;
  padding-right: 15px;
  width: 80px;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.15);
}
.navbar__select p img {
  width: 40px;
  height: 30px;
}

.li-open {
  height: 89px;
  transition: 300ms;
}

.navbar__blog {
  width: fit-content;
  height: fit-content;
  color: rgb(184, 190, 204);
  font-weight: 700;
  width: 72px;
  text-align: center;
}

.logo {
  width: 160px;
  height: 160px;
  left: calc(50% - 80px);
}

.bg_li_header_right {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 10px 32px;
  border-radius: 4px;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0) 0.11%, rgba(255, 255, 255, 0.2) 99.9%);
  font-family: "Mulish";
  font-size: 16px;
  line-height: 24px;
  height: fit-content;
  width: 353px;
}

.bg_li_header_left {
  display: flex;
  align-items: center;
  width: 353px;
  box-sizing: border-box;
  padding: 10px 32px;
  border-radius: 4px;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.2) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  border-radius: 4px;
  font-family: "Mulish";
  font-size: 16px;
  line-height: 24px;
  height: fit-content;
}

@media screen and (max-width: 800px) {
  .navbar__ul {
    padding-left: 24px;
    padding-right: 16px;
  }
  .logo {
    width: 94px;
    height: 94px;
  }
  .logo img {
    width: 94px;
    height: 94px;
  }
  .bg_li_header_left,
  .bg_li_header_right {
    display: none;
  }
}