@font-face {
  font-family: "Halvar Breitschrift";
  src: url("/src/components/assets/fonts/Halvar/HalvarBreit-Lt.ttf");
}
#root {
  z-index: -1;
  position: relative;
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: -1;
  position: relative;
  background-color: #110745;
}

.site {
  background-position: center;
  background-size: cover;
  color: white;
  font-family: "Halvar Breitschrift";
  font-style: normal;
  overflow-x: hidden;
  position: relative;
  z-index: -1;
}

.mobile-bg {
  z-index: -1;
  position: relative;
}

.texture {
  position: absolute;
  height: calc(100% - 1155px);
}

.bg-container {
  position: relative;
}

.header {
  padding-top: 40px;
  box-sizing: border-box;
}

.header__bg {
  position: absolute;
  z-index: -1;
  background-image: url("/public/img/headerbg2.png");
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  height: fit-content;
  box-sizing: border-box;
  height: 1155px;
  width: 100vw;
}

.header__inspector {
  max-width: 100vw;
  padding: 0 14.75%;
  box-sizing: border-box;
}

.trade__container {
  line-height: 56px;
  max-width: 800px;
  text-align: left;
  display: flex;
  flex-direction: column;
  margin-top: 248px;
}

.header__texture {
  position: absolute;
  right: 0;
  bottom: 0;
  top: 0;
  transform: translateY(130px);
  width: 669px;
  height: 688px;
}

.trade__container span {
  font-weight: 700;
  font-size: 48px;
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.to-home-anchor {
  width: fit-content;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 1px;
  height: 1px;
}

.header__btnStart {
  width: 220px;
  height: 56px;
  margin-top: 64px;
}

.header__card-inspector {
  height: fit-content;
  max-width: 100vw;
  margin-top: 208px;
  display: flex;
  gap: 32px;
  align-items: center;
  justify-content: center;
}

.header__card {
  width: 432px;
  max-width: 432px;
  min-height: 256px;
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  border-radius: 2px;
  padding-left: 32px;
  padding-right: 40px;
  border: 1px solid rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(6px);
  position: relative;
}
.header__card img {
  margin-top: 32px;
}
.header__card p {
  margin-top: 56px;
  margin-bottom: 16px;
  font-family: "Halvar Breitschrift";
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
}
.header__card span {
  font-family: "Mulish";
  line-height: 24px;
  color: #b8becc;
}
.header__card .header__card-light {
  margin: 0px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 56px;
  opacity: 0.5;
  filter: blur(90px);
}
.header__card .light1 {
  background: #fef17b;
}
.header__card .light2 {
  background: #5bb9fd;
}
.header__card .light3 {
  background: #78ffae;
}

@media screen and (max-width: 1400px) {
  .trade__container {
    max-width: 600px;
  }
  .trade__container span {
    font-size: 36px;
  }
}
@media screen and (max-width: 1250px) {
  .trade__container {
    max-width: 400px;
  }
  .trade__container span {
    font-size: 26px;
  }
  .header__card-inspector {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 1000px) {
  .header__inspector {
    justify-content: center;
  }
  .trade__container {
    align-items: center;
    margin-top: 650px;
    max-width: 600px;
    text-align: center;
  }
}
@media screen and (max-width: 600px) {
  .mobile-bg {
    background-image: url("/public/img/mobile-texture.png");
    z-index: -1;
    background-size: cover;
    height: calc(100% - 821px);
    width: 100vw;
    position: absolute;
    transform: translateY(821px);
  }
  .header__inspector {
    padding: 0px;
  }
  .header__bg {
    background-image: url("/public/img/headerbg-mobile.png");
  }
  .trade__container {
    margin-top: 250px;
  }
  .trade__container span {
    font-size: 18px;
    line-height: 24px;
  }
  .header__btnStart {
    margin-top: 32px;
  }
  .header__card-inspector {
    margin-top: 500px;
    padding: 0 16px;
    position: relative;
  }
  .header__card {
    background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
    box-sizing: border-box;
    padding: 0 24px;
    min-height: 212px;
    padding-bottom: 40px;
    width: 100%;
  }
  .header__card img {
    margin-top: 24px;
  }
  .header__card p {
    margin-top: 44px;
    font-size: 18px;
    line-height: 24px;
  }
  .header__card span {
    font-size: 14px;
    line-height: 20px;
    width: 16px;
  }
}
.content {
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: -1;
}

.background__situation-deposit {
  max-width: 100vw;
  margin: 0px;
  z-index: 1;
  position: relative;
}

.situation__inspector {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-left: 14.75%;
  flex-wrap: wrap;
  margin-top: 144px;
  z-index: 1;
}

.situation__container {
  width: fit-content;
  width: 692px;
}
.situation__container .situation__everyone {
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 36px;
  line-height: 48px;
  display: inline-block;
}
.situation__container .situation__ul {
  margin-top: 40px;
}
.situation__container .situation__ul li {
  list-style: none;
  margin-top: 32px;
  display: flex;
  align-items: center;
}
.situation__container .situation__ul li span {
  background: url("/public/img/home/question.png") no-repeat;
  background-size: 50px;
  background-position: center;
  min-width: 32px;
  height: 32px;
  display: inline-block;
  margin-right: 32px;
}
.situation__container .situation__ul li:first-child {
  margin: 0px;
}
.situation__container .situation__ul li:last-child {
  text-align: left;
}
.situation__container .situation__paragraph {
  margin-top: 32px;
  margin-bottom: 32px;
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 24px;
  line-height: 32px;
}
.situation__container .situation__proposal {
  padding: 32px 68px 36px 88px;
  border: 1px solid rgba(255, 255, 255, 0.6);
  font-family: "Mulish";
  background: linear-gradient(90.63deg, rgba(255, 255, 255, 0.04) 0.11%, rgba(255, 255, 255, 0) 99.9%);
  border-radius: 2px;
  position: relative;
}
.situation__container .situation__proposal .proposal__marker {
  background: url("/public/img/home/subtract.png");
  background-size: 50px;
  background-position: center;
  min-width: 32px;
  min-height: 32px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 32px;
}

.situation__hole {
  background: url("/public/img/home/starandhole.png");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  width: 60%;
  min-width: 800px;
  min-height: 1075px;
  transform: translateY(-15%);
  right: 0;
  top: 0;
}

@media screen and (max-width: 1400px) {
  .situation__hole {
    min-width: 300px;
    min-height: 400px;
    width: 800px;
    height: 700px;
    transform: none;
  }
  .situation__container .situation__everyone {
    font-size: 28px;
  }
  .situation__container .situation__ul li {
    font-size: 14px;
  }
  .situation__container .situation__paragraph {
    font-size: 28px;
  }
}
@media screen and (max-width: 1250px) {
  .situation__inspector {
    padding: 0px;
    justify-content: center;
  }
  .situation__hole {
    position: relative;
    background-size: cover;
    background-position: top;
    background-image: url("/public/img/home/starandhole-tablet.png");
    width: 100%;
    height: 1250px;
  }
}
@media screen and (max-width: 1000px) {
  .situation__hole {
    height: 1000px;
  }
}
@media screen and (max-width: 800px) {
  .situation__hole {
    height: 800px;
  }
  .situation__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 16px;
  }
  .situation__container .situation__everyone {
    font-size: 18px;
  }
  .situation__container .situation__ul li {
    font-size: 14px;
  }
  .situation__container .situation__ul li span {
    margin: 0 10px;
  }
  .situation__container .situation__paragraph {
    font-size: 18px;
    width: 100%;
    text-align: left;
  }
  .situation__container .situation__proposal {
    padding: 0 24px;
    padding-top: 56px;
    width: 500px;
    height: 168px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .situation__container .situation__proposal .proposal__marker {
    margin: 0;
    left: 0;
    top: 0;
    margin-top: 30px;
    margin-left: 20px;
  }
}
@media screen and (max-width: 600px) {
  .situation__hole {
    background-image: url("/public/img/home/starandhole-mobile.png");
    height: 600px;
  }
  .situation__container .situation__ul li span {
    width: 16px;
    height: 16px;
    background-size: 25px;
    margin: 0 5px;
  }
  .situation__container .situation__proposal {
    padding: 0 12px;
    padding-top: 56px;
    width: 328px;
    font-size: 14px;
  }
  .situation__container .proposal__marker {
    margin: 0;
    margin-top: 30px;
  }
}
@media screen and (max-width: 500px) {
  .situation__container .situation__everyone,
  .situation__container .situation__paragraph {
    text-align: center;
  }
}
