.development__inspector {
  height: 1200px;
  z-index: 2;
  background: url("/public/img/home/bg_grid.png");
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
}
.development__inspector .development__card__container {
  width: 1360px;
  margin-top: 80px;
}
.development__inspector .development__card__container .development__caption {
  margin: 0;
  margin-bottom: 56px;
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 48px;
