  text-align: center;
}
.development__inspector .development__card__container .development__card {
  width: 432px;
  min-width: 350px;
  height: 320px;
  border: 1px solid #afabba;
  border-radius: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.development__inspector .development__card__container .development__card span {
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
