  background-repeat: no-repeat;
}
.development__inspector .development__card__container .three {
  background: url("/public/img/home/light_card3.png");
  background-repeat: no-repeat;
}

.development__card__wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 100vw;
  gap: 30px;
}

@media screen and (max-width: 1250px) {
