}
.practice__text__wrapper .practice__text__caption {
  background: linear-gradient(89.75deg, rgba(255, 255, 255, 0.5) 0.32%, #ffffff 72.39%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
