.questions__card {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  max-width: 896px;
  width: 896px;
  padding-top: 5px;
  padding-left: 20px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.05);
  background-size: cover;
  min-height: 98px;
  border: 1px solid #ffffff96;
  border-radius: 5px;
  padding-bottom: 26px;
}
.questions__default {
  cursor: pointer;
  margin-top: 20px;
  background-image: url("/public/img/home/questions-notOpen.png");
  background-position: center;
  width: 48px;
  height: 48px;
  transition: 500ms;
}
.active {
  cursor: pointer;
  background-image: url("/public/img/home/questions-open.png");
  background-position: center;
  transition: 500ms;
}
.questions__text__container {
  display: flex;
  flex-direction: column;
  max-width: 668px;
  max-height: 80px;
  overflow-y: hidden;
  transition: max-height 0.5s ease-out;
}
.open {
  max-height: 300px;
  transition: 0.5s ease-in;
}
.questions__text {
  margin-top: 30px;
  margin-bottom: 20px;
  color: #ffffff;
  display: flex;
  align-items: center;
  width: fit-content;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
.questions__img {
  background: url("/public/img/home/question.png") no-repeat;
  background-size: 50px;
  background-position: center;
  min-width: 32px;
  height: 32px;
  display: inline-block;
  margin-right: 32px;
}
.questions__text__open {
  padding-left: 62px;
  box-sizing: border-box;
  transition: 200ms;
  transition: opacity 0.3s ease-in-out;
}
@media screen and (max-width: 1000px) {
  .questions__card {
    width: 600px;
  }
  .questions__text__container {
    width: fit-content;
  }
}
@media screen and (max-width: 600px) {
  .questions__card {
    width: 500px;
  }
}
@media screen and (max-width: 500px) {
  .questions__card {
    width: 328px;
    padding: 12px 30px 12px 30px;
    position: relative;
  }
  .questions__text {
    font-size: 14px;
    line-height: 20px;
    min-height:82px;
    margin:0
  }
  .questions__text__open {
    font-size: 14px;
    line-height: 20px;
    padding:0;
  }
  .questions__img {
    min-width: 10px;
    width: 16px;
    height: 16px;
    background-size: 21px;
    background-position: center;
    margin-right:16px
  }
  .open{
  max-height:400px
  }
}
@media screen and (max-width:350px){
  .questions__card{
  width:300px;
  padding: 12px 14px 12px 14px;
  }
  .open{
  max-height:450px
  }
  .questions__default{
  width:24px;
  height:24px;
  margin-top:30px;
  }
}
